div.joystick {
  width: 100%;
  height: 100%;
}

div.controller-container {
  position: absolute;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 30px;
  bottom: 30px;
}

button.exit {
  position: absolute;
  left: 30px;
  top: 50px;
  background-color: rgb(205, 32, 45);
  color: #ffff;
  font-size: 16px;
  padding: 16px 30px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  text-align: center;
}

#move-button-container
{
    position: absolute;
    width: 100%;
    top: auto;
    height: 24vw;
    bottom: 6vh;
}

#right{
    width: 24vw;
    height: 24vw;
    background: url("../images/right_button.svg");
    right: 0px;
}

#left{
    width: 24vw;
    height: 24vw;
    background: url("../images/left_button.svg");
    left:0;
}

#interact{
    width: 15vw;
    height: 15vw;
    background: url("../images/interact_button.svg");
    left: 0; 
    right: 0;
    bottom: 0;
    margin: auto;
}
  
#left, #right, #interact {
    position: absolute;
    -webkit-appearance: none;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
    background-color: #D9D9D9;
    border-radius: 50%;
    border: none;
}

.up-button, .press-button{
    /* disable user select */
    -ms-user-select: none; 
    -moz-user-select: none; 
    -webkit-user-select: none; 
    user-select: none;
    border: none;
    background: #525252;
    font-family: "Roboto";
    font-size: 20px;
    color: white;
    border-radius: 7px;
    box-shadow: 0 7px 0px #242424;
    display: inline-block;
    transition: all .2s;
    position: relative;
    padding: 20px 25px;
    position: relative;
    top: 0;
    cursor: pointer;
    margin:0 20px;
}

.press-button{
    top: 3px;
    box-shadow: 0 2px 0px #121212;
    transition: all .2s;
}